/*--------------------admin-search, admin-edit----------------------*/
.admin-search {}
.admin-search__wrapper {}
.admin-search__form {
	margin-bottom: rem(32);
	position: relative;
	display: flex;
	align-items: center;
	background: #FFF;
	box-shadow: 0px 4px 14px 0px #E5E5E5;
	border-radius: 7px;

	@include min-screen($xs){
		
		padding: 0 rem(16) 0 0;
	}

	@include max-screen($sm){
		flex-wrap: wrap;	
	}
	.icon{
		position: absolute;
		top: rem(20);
		// transform: translateY(-50%);
		color: $icons;
		left: rem(16.76);
		font-size: rem(20.48);
	}

}
.admin-search__label {
	display: block;
	flex: 1 1 auto;
}
.admin-search__input {
	padding-left: rem(81);
	height: rem(57);
	@include placecolor(#C4C4C4);
	color: #C4C4C4;
	font-family: 'Roboto', sans-serif;
	@include adaptive-value-rem('font-size', 20, 16, $maxWidth, $minWidth, 1);
	font-weight: 700;
	border-radius: 7px;

	@include max-screen($xs){
		padding-left: rem(50);
	}
}
.admin-search__submit {
	flex: 0 0 rem(142);

	@include max-screen($xs){
		flex: 1 1 100%;
		margin: 0 rem(16) 0 rem(16);
		margin-bottom: rem(10);
	}
}
.search-results{
	padding: rem(37) rem(42);
	position: absolute;
	left: 0;
	top: calc(100% + rem(47));
	width: 100%;
	max-height: rem(339);
	border-radius: rem(10);
	background: #FFF;
	box-shadow: 0px 4px 35px 0px #79D1D5;
	overflow-y: auto;
	transition: transform .3s;
	visibility: hidden;
	opacity: 0;
	transform: translateY(rem(-10));
	z-index: 2;

	.search-visible &{
		visibility: visible;
		opacity: 1;
		transform: translateY(0);
	}
}
.search-results__item{
	height: rem(61);
	background-color: #BCFCFF;
	display: flex;
	align-items: center;
	padding: rem(5) rem(30);
	
	&:not(:last-child){
		margin-bottom: rem(10);
	}

	@media (any-hover: hover) { //только для устройств с ховером
			cursor: pointer;
			&:hover{
	
			}
		}
}
/*--------------------END admin-search, admin-edit----------------------*/ 